/**
 * Body
 */
body {
  font-family: sans-serif;
  background: #fff;
  color: #000;
  font-size: 16px;
  line-height: 140%; }

/**
 * Hide's the element from the page, but screen readers will read it
 */
.hidden,
.screen-reader-text {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  overflow: hidden;
  height: 1px;
  width: 1px; }

.skip-link.screen-reader-text:focus {
  display: block;
  clip: auto;
  color: #000;
  height: auto;
  left: 5px;
  top: 5px;
  padding: 10px 20px;
  text-decoration: none;
  width: auto;
  z-index: 100000; }

/**
 * Clear
 */
.clear {
  clear: both; }

/**
 * Workaround a Windows Phone responsive issuee
 */
@media screen {
  @-ms-viewport {
    width: device-width; } }

