a {
  color: #b02448; }
  a:focus, a:hover {
    text-decoration: none; }

/**
 * Lists
 */
ul {
  list-style: disc outside;
  margin: 0 0 20px;
  padding: 0; }
  ul li {
    margin: 0 0 0 20px; }
    ul li ul {
      margin: 0; }

ol {
  list-style: decimal outside;
  margin: 0 0 20px;
  padding: 0; }
  ol li {
    margin: 0 0 0 20px; }
    ol li ol {
