  -o-tab-size: 2;
  tab-size: 2;
  overflow: scroll; }

code {
  padding: 5px;
  margin: 0 2px; }

/**
 * Input fields
 */
input[type=text],
input[type=url],
input[type=email],
input[type=tel],
input[type=number],
input[type=password],
input[type=search] {
  font-family: sans-serif;
  appearance: none;
  width: 100%;
  border: 1px solid #cccccc;
  border-radius: 0;
  font-size: 16px;
  line-height: 140%;
  padding: 10px;
  margin: 0;
  display: block; }

/**
 * Textarea
 */
textarea {
  font-family: sans-serif;
  appearance: none;
  width: 100%;
  max-width: 100%;
  height: 100px;
  border: 1px solid #cccccc;
  border-radius: 0;
  font-size: 16px;
  padding: 10px;
  margin: 0;
  display: inline-block; }

/**
 * Buttons
 */
.button,
a.button {
  font-family: sans-serif;
  appearance: none;
  border: 1px solid #cccccc;
  border-radius: 0;
  background: #cccccc;
  color: #fff;
  margin: 0;
  padding: 10px;
  font-size: 16px;
  line-height: 140%;
  text-decoration: none;
  cursor: pointer;
  display: inline-block; }
  .button:focus, .button:hover,
  a.button:focus,
  a.button:hover {
    background: #cccccc; }
  .button:disabled, .button:disabled[disabled],
  a.button:disabled,
  a.button:disabled[disabled] {
    cursor: not-allowed;
    opacity: .5; }

